import Team from './Team';

/**
 * Ð¤Ð¾Ñ€Ð¼Ð¸Ñ€ÑƒÐµÑ‚ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€ Ð¿ÐµÑ€ÑÐ¾Ð½Ð°Ð¶Ð° Ð¸Ð· Ð¼Ð°ÑÑÐ¸Ð²Ð° allowedTypes ÑÐ¾
 * ÑÐ»ÑƒÑ‡Ð°Ð¹Ð½Ñ‹Ð¼ ÑƒÑ€Ð¾Ð²Ð½ÐµÐ¼ Ð¾Ñ‚ 1 Ð´Ð¾ maxLevel
 *
 * @param allowedTypes Ð¼Ð°ÑÑÐ¸Ð² ÐºÐ»Ð°ÑÑÐ¾Ð²
 * @param maxLevel Ð¼Ð°ÐºÑÐ¸Ð¼Ð°Ð»ÑŒÐ½Ñ‹Ð¹ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ñ‹Ð¹ ÑƒÑ€Ð¾Ð²ÐµÐ½ÑŒ Ð¿ÐµÑ€ÑÐ¾Ð½Ð°Ð¶Ð°
 * @returns Ð³ÐµÐ½ÐµÑ€Ð°Ñ‚Ð¾Ñ€, ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð¿Ñ€Ð¸ ÐºÐ°Ð¶Ð´Ð¾Ð¼ Ð²Ñ‹Ð·Ð¾Ð²Ðµ
 * Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ Ð½Ð¾Ð²Ñ‹Ð¹ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€ ÐºÐ»Ð°ÑÑÐ° Ð¿ÐµÑ€ÑÐ¾Ð½Ð°Ð¶Ð°
 *
 */
export function* characterGenerator(allowedTypes, maxLevel) {
  const index = Math.floor(Math.random() * allowedTypes.length);
  const level = Math.floor(Math.random() * maxLevel + 1);
  yield new (allowedTypes[index])(level);
}

/**
 * Ð¤Ð¾Ñ€Ð¼Ð¸Ñ€ÑƒÐµÑ‚ Ð¼Ð°ÑÑÐ¸Ð² Ð¿ÐµÑ€ÑÐ¾Ð½Ð°Ð¶ÐµÐ¹ Ð½Ð° Ð¾ÑÐ½Ð¾Ð²Ðµ characterGenerator
 * @param allowedTypes Ð¼Ð°ÑÑÐ¸Ð² ÐºÐ»Ð°ÑÑÐ¾Ð²
 * @param maxLevel Ð¼Ð°ÐºÑÐ¸Ð¼Ð°Ð»ÑŒÐ½Ñ‹Ð¹ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ñ‹Ð¹ ÑƒÑ€Ð¾Ð²ÐµÐ½ÑŒ Ð¿ÐµÑ€ÑÐ¾Ð½Ð°Ð¶Ð°
 * @param characterCount ÐºÐ¾Ð»Ð¸Ñ‡ÐµÑÑ‚Ð²Ð¾ Ð¿ÐµÑ€ÑÐ¾Ð½Ð°Ð¶ÐµÐ¹, ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ðµ Ð½ÑƒÐ¶Ð½Ð¾ ÑÑ„Ð¾Ñ€Ð¼Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ
 * @returns ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€ Team, Ñ…Ñ€Ð°Ð½ÑÑ‰Ð¸Ð¹ ÑÐºÐ·ÐµÐ¼Ð¿ Ð¿ÐµÑ€ÑÐ¾Ð½Ð°Ð¶ÐµÐ¹. ÐšÐ¾Ð»-Ð²Ð¾ Ð¿ÐµÑ€ÑÐ¾Ð½Ð°Ð¶ÐµÐ¹ Ð² ÐºÐ¾Ð¼Ð°Ð½Ð´Ðµ - characterCount
 * */
export function generateTeam(allowedTypes, maxLevel, characterCount) {
  const actualCharacters = [];
  for (let i = 0; i < characterCount; i += 1) {
    const playerGenerator = characterGenerator(allowedTypes, maxLevel);
    const newChar = playerGenerator.next().value;
    actualCharacters.push(newChar);
  }
  return new Team(actualCharacters);
}
